import React, {useState, useEffect} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import {RFID_API_URL, API_URL} from '../../config';
import {Table,Button} from 'react-bootstrap';


import HotelNavbar from './HotelNavbar';
import StocksNavbar from './customer.stock.navbar';
import Loading from '../main/LoadingPage';

export default function HotelStock(props) {
    let id = props.match.params.id;

    const [loggedInUser, setLoggedInUser] = useState (null);
    const [stock, setStock] = useState(null);
    const [bulkStock, setBulkStock] = useState(null);
    const [redirecting,setRedirecting] = useState (false);
    const [showDate, setShowDate] = useState(false);
    const [showWashes, setShowWashes] = useState(false);
    const [stockByDate, setStockByDate] = useState(null);
    const [stockByWashes, setStockByWashes] = useState(null);

    useEffect(()=>{
        axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggedInUser(result.data)
        })
        .catch(() => {
            setRedirecting(true)
        })
        if(props){
            axios.get(`${API_URL}/customer/${id}/stock`,{withCredentials: true})
            .then((response)=>{
              console.log(response.data)
              setStock(response.data);
            })
        }
    },[])

    const handleByDate = () =>{
        setShowDate(true);
        if(!stockByDate){
        axios.get(`${API_URL}/customer/${id}/stock/bydate`,{withCredentials: true})
        .then((response)=>{
          setStockByDate(response.data);
        })
        }
    }

    const handleSortByDate = () =>{
        const stocksortedByDte = JSON.parse(JSON.stringify(stockByDate))
        stocksortedByDte.sort((a,b)=> new Date(b.fullDate)-new Date(a.fullDate));
        setStockByDate(stocksortedByDte);
    }

    const handleSortByWashes = () =>{
        const stocksortedByWahses = JSON.parse(JSON.stringify(stockByDate))
        stocksortedByWahses.sort((a,b)=> b.lavados-a.lavados);
        setStockByDate(stocksortedByWahses);
    }

    if(redirecting) return <Redirect to={'/'}/>
    if(!stock) return <Loading/>
    if(showDate && !stockByDate) return <Loading/>
    //FixStock
    // albaran.forEach(elem => {
    //     let articulo;
    //     let cantidad;
    //     axios.put(`https://smartlinen.loca.lt/sqlquery`,{sqlquery: `select * from articulos where id=${elem.articulo_id}`})
    //     .then((resArticle)=>{
    //         articulo = resArticle.data;
    //         axios.put(`https://smartlinen.loca.lt/sqlquery`,{sqlquery: `select count(serials_id) from serials_lineaenvio where lineaenvio1_id=${elem.id}`})
    //         .then((resQuantity)=>{
    //             cantidad = resQuantity.data;
    //             if(!albaranRepresentado) setAlbaranRepresentado([{articulo, cantidad}]);
    //             else {albaranRepresentado.push({articulo, cantidad})}
    //         })
    //     })
    // });
    //if(!albaranRepresentado)return <p>Loading...</p>
    //console.log(albaranRepresentado)
    return (
        <>
                <HotelNavbar loggedInUser={loggedInUser} logOut = {props.logOut}/>
                <StocksNavbar loggedInUser={loggedInUser} byDate={handleByDate}/>
                {
                    showDate? (
                        <Table striped bordered hover variant="dark" style={{marginTop:'10px', textAlign: 'center'}} id='ComplexLaundriesTable'  >
                        <thead>
                            <tr>
                            <th>Artículo</th>
                            <th>Alta<Button style={{backgroundColor:'transparent', border:'none'}} onClick={handleSortByDate}>^</Button></th>
                            <th onClick={handleSortByWashes}>Lavados<Button style={{backgroundColor:'transparent', border:'none'}} onClick={handleSortByDate}>^</Button></th>
                            </tr>
                        </thead>
                        {
                            stockByDate? (stockByDate.map((elem,i)=>{
                                return(
                                    <tbody key={i+'alb_art'}>
                                        <tr>
                                            <td>{elem.laundry}</td>
                                            <td>{elem.day.toString().padStart(2,0)}-{elem.month.toString().padStart(2,0)}-{elem.year}</td>
                                            <td>{elem.lavados}</td>
                                        </tr>
                                    </tbody>
                                    
                                )}
                            )):''
                        }
                        </Table>
                    ):(
                        <Table striped bordered hover variant="dark" style={{marginTop:'10px', textAlign: 'center'}} id='ComplexLaundriesTable'  >
                                <thead>
                                    <tr>
                                    <th>Artículo</th>
                                    <th>Cantidad</th>
                                    </tr>
                                </thead>
                        {
                            stock? (stock.map((elem,i)=>{
                                if(elem.cantidad !==0){
                                return(
                                    <tbody key={i+'alb_art'}>
                                        <tr>
                                            <td>{elem.articulo}</td>
                                            <td>{elem.cantidad}</td>
                                        
                                        </tr>
                                    </tbody>
                                    
                                )}
                            })):''
                        }
                        </Table>
                )
                }
        </>
    )
}
