import React,{useEffect, useState} from 'react';
import axios from 'axios';
import {API_URL} from '../../config';
import {Button, Card} from 'react-bootstrap';
import {Redirect,Link} from 'react-router-dom';


import Navbar2 from './Laundry.2.Navbar';
import LaundryNavbar from './LaundryNavbar';
import Loading from '../main/LoadingPage';

export default function HotelHome(props) {
    const [loggedInUser, setLoggedInUser] = useState(null);
    const [albaranes, setAlbaranes] = useState(null);
    const [redirecting,setRedirecting] = useState (false);
    const [envios, setEnvios] = useState(null);
    const [redirectToOrder, setRedirectToOrder] = useState(false);
    const [lastOrderId, setLastOrderId] = useState(null);
    const [complexes, setComplexes] = useState(null);

    const cardHeaderStyle= {
        display:'flex',
        flexDirecction: 'row',
        justifyContent: 'space-between'
    }

    useEffect(()=>{
        axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggedInUser(result.data)
          let today = new Date();
          console.log(today)
          let year = today.getFullYear();
          let month = today.getMonth()+1;
          console.log(month)
            axios.get(`${API_URL}/customer/envios/${year}/${month}`,{withCredentials: true})
            .then((response)=>{
                setEnvios(response.data)
            })
            axios.get(`${API_URL}/complexes`,{withCredentials:true})
                .then((complexes)=>{
                    setComplexes(complexes.data)
                })
        }) 
        .catch(()=>{
            setRedirecting(true)
        })
    },[])

    
    
    const handleFilterByDate = (e) =>{
        e.preventDefault();
        const {month, year} = e.currentTarget;
        let date = {month,year};
        console.log(month.value,year.value)
        axios.get(`${API_URL}/customer/envios/${year.value}/${month.value}`)
        .then((enviofiltered)=>{
            setEnvios(enviofiltered.data)
        })
    }

    if(redirecting) return <Redirect to={'/'}/>
    if(!loggedInUser) return <Loading/>
    if(redirectToOrder) return(<Redirect to={`/customer/service/${lastOrderId}/edit`}/>)
    if(!envios || !complexes) return (<>
        <LaundryNavbar loggedInUser={loggedInUser} logOut = {props.logOut}/>
        <Loading/>
        </>)
    envios.sort((a,b)=> b.id-a.id)
    return (
        <>
            <LaundryNavbar loggedInUser={loggedInUser} logOut = {props.logOut}/>
            <Navbar2 loggedInUser={loggedInUser} onFilter= {handleFilterByDate}/>
            <div style={{marginBottom: '50px'}}>
            {
                
                envios.map((elem,i)=>{
                    console.log(complexes)
                    let complex = complexes.find((complexesElem)=>{
                        return complexesElem.rfidId === elem.cliente_id;
                    })
                    console.log(complex)
                    return(
                        <Card key ={'alb' + i} style={{margin:'20px'}}>
                            <Card.Header><div style={cardHeaderStyle}><p>{elem.codigo}</p><p>{complex.name}</p><p>{elem.fecha.slice(0,10)}</p></div></Card.Header>
                            <Card.Body><Link to={`/laundry/albaran/${elem.id}`}><Button>Detalles</Button></Link></Card.Body>
                        </Card>
                    )
                })
            }
            </div>
            {///Creacion de pedidos manuales no RFID.
            /* <div style={{textAlign: 'center', margin:'10px'}}>
            <Button onClick={handleOpenNewService}>Nuevo servicio</Button>
            <hr/>
            </div>
            {
                orders.map((elem,i)=>{
                    return <OrderCard key={'order'+i} order={elem}/>
                })
            } */}



        </>
    )
}
