import React,{useEffect, useState} from 'react';
import axios from 'axios';
import {API_URL} from '../../config';
import {Button, Card} from 'react-bootstrap';
import {Redirect,Link} from 'react-router-dom';


import HotelNavbar from './HotelNavbar';
import OrderCard from './OrderCard';
import AlbaranDetails from './AlbaranDetails';
import TrazabilidadNavbar from './Trazabilidad.navbar';
import Loading from '../main/LoadingPage';

export default function HotelHome(props) {
    const [loggedInUser, setLoggedInUser] = useState(null);
    const [albaranes, setAlbaranes] = useState(null);
    const [redirecting,setRedirecting] = useState (false);
    const [envios, setEnvios] = useState(null);
    const [redirectToOrder, setRedirectToOrder] = useState(false);
    const [lastOrderId, setLastOrderId] = useState(null);

    const cardHeaderStyle= {
        display:'flex',
        flexDirecction: 'row',
        justifyContent: 'space-between'
    }

    useEffect(()=>{
        axios.get(`${API_URL}/user`, {withCredentials: true})
        .then((result) => {
          setLoggedInUser(result.data)
            axios.get(`${API_URL}/customer/${result.data.rfidComplexId}/envios`,{withCredentials: true})
            .then((response)=>{
                setEnvios(response.data)
            })
        })
        .catch(()=>{
            setRedirecting(true)
        })
    },[])
    const handleOpenNewService = () =>{
        console.log(loggedInUser);
        const complexId = loggedInUser.complex;
        const CreatedBy = loggedInUser._id;
        axios.post(`${API_URL}/customer/neworder`,{complexId, CreatedBy, status: 'ordering'}, {withCredentials: true})
        .then((result) => {
            setLastOrderId(result.data._id)
            setRedirectToOrder(true)
        })
    }
    
    const handleFilterByDate = (e) =>{
        e.preventDefault();
        const {month, year} = e.currentTarget;
        let date = {month,year};
        console.log(month.value,year.value)
        axios.get(`${API_URL}/customer/${loggedInUser.rfidComplexId}/envios/${year.value}/${month.value}`)
        .then((enviofiltered)=>{
            setEnvios(enviofiltered.data)
        })
    }

    if(redirecting) return <Redirect to={'/'}/>
    if(!loggedInUser) return <Loading/>
    if(redirectToOrder) return(<Redirect to={`/customer/service/${lastOrderId}/edit`}/>)
    return (
        <>
            <HotelNavbar loggedInUser={loggedInUser} logOut = {props.logOut}/>
            <TrazabilidadNavbar loggedInUser={loggedInUser} />
            <p style={{textAlign:'center', margin:'100px 0px'}}>Servicio temporalmente no disponible</p>
        </>
    )
}
